

/* Sections */

.imageContainer {
	position: relative;
	display: grid;
}
.screenshot {
	max-width: 300px;
	position: relative;
	grid-column: 1;
	grid-row: 1;
	box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
	transition: all 1s ease!important;
}

.body {
	animation: fade 1s ease both;
}

.numbering {
	position: relative;
	left: -2ch;
	bottom: -1.2em;
}

.shifted {
	position: relative;
	top: -1.2em;
}

@keyframes fade {
	from {
		opacity: 0
	}
	to {
		opacity: 1
	}
}

/* Arrows */

.arrows {
	margin: 1em 0;
}

.arrows * {
	cursor: pointer;
}

/* Dots */

.dots {
	position: absolute;
	bottom: 80px;
	display: grid;
	grid-auto-flow: column;
	column-gap: 10px;
}

.dot {
	cursor: pointer;
}