:global(#home) {
	background-color: var(--green);
	color: white;
	padding-bottom: 150px;
	overflow: hidden;
	position: relative;
}
:global(#home) :global(.wrapper) > * {
	text-align: center;
	width: 100%;
	box-sizing: border-box;
	z-index: 1;
}

@media only screen and (max-width: 700px) {
	:global(#home) {
		min-height: 50vh;
	}
}

:global(#works) {
	background-color: white;
	margin-top: -120px;
	margin-bottom: -80px;
}

@media only screen and (max-width: 700px) {
	section:global(#works) {
		margin-top: unset;
		margin-bottom: unset;
	}
}

:global(#works-text) {
	background-color: #fce7ea;
	padding-top: 100px;
}
:global(#notice) {
	background-color: var(--red);
	color: white;
	padding-top: 50px;
}

.description {
	color: rgba(20, 20, 20. 0.8);
	display: block;
	font-size: 18px;
	line-height: 1.5em;
}

.title {
	font-weight: 700;
	font-size: 48px;
}
.subtitle {
	width: 100%;
	padding-top: 20px;
	font-weight: 400;
	font-size: x-large;
}

/* Cards */

.card {
	display: grid;
	width: 100%;
	row-gap: 20px;
	z-index: 1;
	justify-content: center;
	justify-items: center;
	align-items: center;
}

.card img {
	width: 80%;
}

/* Notice */

:global(#notice) :global(.wrapper) > * {
	text-align: center;
	width: 100%;
}