:global(#industries) {
	background-color: var(--green);
	color: white;
	padding-bottom: 100px;
	overflow: hidden;
	position: relative;
}
:global(#industries) :global(.wrapper) > *, :global(#action) :global(.wrapper) > * {
	text-align: center;
	width: 100%;
	box-sizing: border-box;
	z-index: 1;
}

:global(#action) {
	background-color: var(--green);
	color: white;
	padding: 30px 0;
}

section.divider {
	background-color: var(--green);
	padding: 0px;
	min-height: 20px;
}

#started, #impact, #data {
	padding: 20px 0;
}

#impact {
	background-color: var(--blue);
	color: white;
}

/* Image Container */

.imgContainer {
	width: 100%;
	height: 100%;
	position: relative;
}

@media only screen and (max-width: 700px) {
	.imgContainer {
		display: grid;
		grid-auto-flow: row;
		justify-content: stretch;
		row-gap: 20px;
	}
	div.imgContainer img {
		position: relative;
		left: unset;
		top: unset;
		max-width: unset;
		width: 100%;
	}
}

.imgContainer img {
	max-width: 200px;
	top: 0px;
	position: absolute;
	border-radius: 5px;
	box-shadow: 1px 1px 4px var(--shadow-red);
}
.imgContainer img {
	top: unset;
	bottom: 0px;
	left: calc(50% - 100px);
}

.imgContainer img:first-child {
	left: 0px;
	bottom: unset;
}
.imgContainer img:last-child {
	left: unset;
	right: 0px;
	bottom: unset;
}
.imgContainer img:not(:first-child):not(:last-child) {
	z-index: 2;
}
/* Boxes */

/* Boxes: Depth 1 */

.columns {
	display: grid;
	grid-auto-flow: column;
	column-gap: 50px;
	row-gap: 50px;
	grid-auto-columns: 1fr 1fr;
	position: relative;
}
@media only screen and (max-width: 700px) {
	.columns {
		grid-auto-flow: row;
	}
}

.boxContainer {
	display: grid;
	row-gap: 30px;
	column-gap: 30px;
	grid-auto-flow: column;
	grid-auto-columns: 1fr;
}

@media only screen and (max-width: 700px) {
	.boxContainer {
		grid-auto-flow: row;
	}
}

.arrow {
	position: absolute;
	z-index: 2;
	left: calc(50% - 50px);
	top: calc(50%);
	width: 100px;
}

/* Boxes: Depth 2 */

.box {
	border-radius: 20px;
	border: 2px solid var(--red);
	box-shadow: 2px 2px 6px var(--shadow-red);
	padding: calc(20px + 2em) 20px 20px 20px;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	max-width: 500px;
	background-color: white;
	color: black;
	top: 0px;
	transition: top 0.333s ease;
}
.box:hover {
	top: -5px;
}

.box img {
	width: max-content;
}

.box .head {
	position: absolute;
	top: -1.5em;
	background-color: var(--blue);
	color: white;
	padding: 20px 40px;
	box-sizing: border-box;
	border-radius: 20px;
	font-weight: bold;
}
.box .description {
	text-align: left;
}
#impact .box .head {
	background-color: var(--red);
}
#data .box .head {
	background-color: var(--yellow);
}

.column {
	display: grid;
	row-gap: 20px;
}

.column .box {
	padding: 10px;
	font-size: small;
}