
:global(#subscribe) {
	background-color: var(--green);
	color: white;
	margin-bottom: -100px;
	position: relative;
	overflow: hidden;
}
:global(#subscribe) :global(.wrapper) {
	z-index: 2;
}
#spacer {
	background-color: var(--blue);
	min-height: 50px;
	z-index: 1;
}

/* Form */

.formContainer {
	display: flex;
	justify-content: center;
	width: 100%;
	max-width: max-content;
	padding: 20px 30px;
	box-sizing: border-box;
	color: black;
	background-color: white;
	border-radius: 10px;
}
.form {
	display: grid;
	grid-auto-flow: row;
	text-align: left;
	width: 500px;
	max-width: 100%;
	row-gap: 10px;
}

.form > :not(input):not(label) {
	text-align: center;
	font-size: 16px;
}
.form h2:not(input) {
	font-size: 20px;
}

.form input, .form textarea {
	outline: none;
	border: none;
	border-radius: 5px;
	min-height: 2em;
	padding: 10px 20px;
	box-sizing: border-box;
	border: var(--green) 2px solid;
	border-radius: 5px;
	font-style: italic;
	transition: background-color 0.33s ease;
}
.form input:hover, .form textarea:hover {
	background-color: #02d184;
}
.form label {
	display: flex;
	align-items: center;
	text-align: left;
	font-size: 16px;
}
.form label a {
	font-weight: normal;
}
.form input[type='checkbox'] {
	color: transparent;
	margin: 0 10px;
	border: var(--green) 2px solid;
}

.form textarea {
	min-height: 10em;
}

.form input[type='submit'] {
	border: none;
	background-color: white;
	font-family: 'Montserrat',"Source Sans Pro","Open Sans","San Francisco",Helvetica,Arial,sans-serif;
	font-size: small;
	background-color: #02d18440;
	transition: background-color 0.33s ease;
}
.form input[type='submit']:hover {
	background-color: var(--green);
}