.header {
    position: fixed;
    top: 0px;
    height: 10vh;
    width: 100vw;
    z-index: 4; /* On account on react-carousel */
    
    display: grid;
    grid-template-columns: 12vw auto 1fr auto auto;
    grid-column-gap: 3vw;
    align-items: start;
    font-size: 20px;
    transition: height 1s ease, box-shadow 1s ease;
    color: white;
    background-color: var(--green);
    white-space: pre;
}
.header.scrolled {
    height: 8vh;
    box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.5);
}

.header > div.logoWrapper {
    position: relative;
    height: 15vh;
    margin-left: 20%;
    display: flex;
    flex-direction: row;
    justify-content: center;
	width: 80%;
	
}

.logo {
	height: 100%;
	pointer-events: none;
}


.header > div {
    width: 100%;
    height: inherit;
}

/* Mobile */

.mobileMenu {
    background-color: var(--blue);
    color: white;
	position: fixed;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	z-index: 4;
	overflow: hidden;
    max-height: 100vh;
    max-width: 100vw;
    top: 0px;
    right: 0px;
    border-radius: 0%;
	transition: all 0.5s ease;
}

@media only screen and (max-width: 700px) {
	.mobileMenu.collapsed {
        max-height: 8vh;
        max-width: 8vh;
        border-radius: 50%;
    }
    .mobileMenu.collapsed > *:not(.menuButton) {
        display: none;
    }
}
.closeButton {
    font-size: large;
    background-color: transparent;
    border: none;
    border-radius: 50%;
    color: white;
    padding: 20px;
    position: relative;
    animation: fade-in-from-left 1s ease 0.5s both;
}
.menuButton {
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 1.5vh;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 5vh;
	z-index: 3;
    animation: fade-in 1s ease both 0.5s;
}

.mobileMenu a {
    color: white;
    text-emphasis: none;
	text-decoration: none;
	border-bottom: rgba(50, 50, 50, 0.4) solid 2px;
	padding: 20px;
    width: 80%;
    position: relative;
    animation: fade-in-from-left 1s ease both;
}
@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fade-in-from-left {
    from {
        left: -100vw;
    }
    to {
        left: 0vw;
    }
}

.mobileMenu hr {
	background-color: white;
	width: 80%;
	height: 5px;
}

.buttonContainer {
    display: grid;
    grid-auto-columns: auto;
    grid-auto-flow: column;
    grid-column-gap: 5vw;
    align-items: center;
}

.button {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-weight: 500;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid rgba(128, 128, 128, 0);
}
.header.scrolled .button {
    border-radius: 5px;
}
.header:not(.scrolled) .button {
    border-bottom: 1px solid rgba(128, 128, 128, 0.39);
    text-underline-offset: 2px;
}
.header:not(.scrolled) .button:hover {
    border-bottom: 1px solid rgba(128, 128, 128, 0.9);
}
.header.scrolled .button:hover {
    border: 1px solid rgba(128, 128, 128, 0.5);
}
.button#subscribe {
    background-color: var(--red);
}

.header .nameWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: inherit;
    font-size: larger;
    text-align: left;
    position: relative;
}

.header div.name {
    height: auto;
    display: flex;
    flex-direction: row;
    text-align: left;
    margin: 12px 0;
    position: relative;
}

@media only screen and (max-width: 700px) {
    div.header {
        grid-template-columns: 80fr 20fr;
        grid-column-gap: 0%;
    }
}

.header.scrolled a:link, .header.scrolled a:visited, .header.scrolled a:hover, .header.scrolled a:active {
    color: white;
}
.header a:link, .header a:visited, .header a:hover, .header a:active {
    /*color: black;*/
    color: white;
    text-emphasis: none;
    text-decoration: none;
}

div.rings {
    left: 8.5ch;
    bottom: 1.5em;
    position: absolute;
    transform: scale(0.5);
}