}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
/* Depth: 0 */
html {
	scroll-behavior: smooth;
}

#root {
	display: flex;
	font-family: 'Montserrat',"Source Sans Pro","Open Sans","San Francisco",Helvetica,Arial,sans-serif;
	text-align: center;
    justify-content: center;
	font-size: larger;
	background-color: #f6f8f7;
	--light-green: #02d18430;
	--green: #02d184;
	--red: #ff3055;
	--shadow-red: #ff305530;
	--light-blue: #00dfdf;
	--blue: #0575E6;
	--dark-blue: rgb(23, 98, 160);
	--yellow: #FFC800;
}

/* Depth: 1 */

.bgRings {
	transform: scale(8);
	bottom: -38%;
	left: 20vw;
	z-index: 0;
	opacity: 0.2;
}

.main {
	display: flex;
	flex-direction: column;
	padding-top: 8vh;
	z-index: 1;
	width: 100%;
}
.main * {
	scroll-margin-top: 6vh;
}

/* Depth: 2 */

.main > section, .main > div {
    flex: 0 1 auto;
	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 50px 0px 30px 0px;
}

.main > section a:link,
.main > section  a:visited,
.main > section a:hover,
.main > section a:active {
	color: inherit;
	-webkit-text-emphasis: none;
	        text-emphasis: none;
	text-decoration: none;
	cursor: pointer;
}
.main > section a {
	font-weight: bold;
}

.main section .wrapper > * {
	margin: 20px 0 10px 0;
}

@media only screen and (max-width: 700px) {
	.App {
		display: flex;
		flex-direction: column;
	}
	@supports not ((display: flex) or (display: grid)) {
		.main {
			display: flex;
		}
	}
}

/* Depth: 3 */

.wrapper {
	width: 980px;
	max-width: 95%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.wrapper > :not(div):not(a):not(img) {
	align-self: flex-start;
	text-align: left;
}

/* Depth: 4 */

.columns {
	display: grid;
	width: 100%;
	grid-template-columns: 1fr 1fr;
	grid-column-gap: 50px;
	-webkit-column-gap: 50px;
	        column-gap: 50px;
	grid-row-gap: 30px;
	row-gap: 30px;
	position: relative;
}
@media only screen and (max-width: 600px) {
	.columns {
		grid-template-columns: unset;
		grid-auto-rows: auto;
	}

}
.column-text {
	height: -webkit-max-content;
	height: -moz-max-content;
	height: max-content;
	display: flex;
	flex-direction: column;
	text-align: left;
	box-sizing: border-box;
	align-self: center;
	align-items: stretch;
	justify-content: center;
	position: relative;
}

/* Additional */

.column-text > *:first-child {
	-webkit-margin-before: 0px;
	        margin-block-start: 0px;
}
p:first-of-type {
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
}
ol, ul {
	-webkit-margin-before: 0.5em;
	        margin-block-start: 0.5em;
	-webkit-margin-after: 0px;
	        margin-block-end: 0px;
}

.normal {
	font-weight: normal;
}

.highlighted {
	color: rgb(233, 198, 0);
}
@supports (-webkit-text-stroke: 1px black) {
	.highlighted {
	  -webkit-text-stroke: 0.5px black;
	}
}

.bold {
	font-weight: bold;
}

.bolder {
	font-weight: bolder;
}

.underlined {
	text-decoration: underline;
}

.strikethrough {
	text-decoration: line-through;
}

.main label, .light {
	font-weight: 300;
}

/* Buttons */

.buttonSection {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	justify-content: center;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}

